
<template>
<div id="table">
    <router-view>
    </router-view>
    <div id="same_game">
        <h1>   </h1>
        <div id="infoDiv">
            <button v-on:click="gameStart"></button>
            <span> : <span id='score'>0</span></span>
            <div id='info'>   <br></div>
        </div>
        <div id="gameDiv">
            <div id='countDown'>ready</div>
            <table id="cardTable"></table>
        </div>
    </div>
</div>
</template>
<script src="https://ajax.googleapis.com/ajax/libs/jquery/3.4.1/jquery.min.js"></script>
<script>
//  
var gameState = ''

//  src
var openCardId1 = ''
var openCardId2 = ''

//   
function generateRandom (min, max) {
    var ranNum = Math.floor(Math.random()*(max-min+1)) + min
        return ranNum
}

var cards   //  
var score = 0   // 
var openedCtn = 0    //   

//  
function setTable() {
    cards = [
        require('../../assets/find_same_picture/bull.png'), require('../../assets/find_same_picture/bull.png'),
        require('../../assets/find_same_picture/chick.png'), require('../../assets/find_same_picture/chick.png'),
        require('../../assets/find_same_picture/crab.png'), require('../../assets/find_same_picture/crab.png'),
        require('../../assets/find_same_picture/fox.png'), require('../../assets/find_same_picture/fox.png'),
        require('../../assets/find_same_picture/hedgehog.png'), require('../../assets/find_same_picture/hedgehog.png'),
        require('../../assets/find_same_picture/hippopotamus.png'), require('../../assets/find_same_picture/hippopotamus.png'),
        require('../../assets/find_same_picture/koala.png'), require('../../assets/find_same_picture/koala.png'),
        require('../../assets/find_same_picture/lemur.png'), require('../../assets/find_same_picture/lemur.png')
    ]
    var cardTableCode = '<tr>'
    for(var i=0; i<16; i++){
        if(i>0 && (i%4 == 0)){
            cardTableCode += '</tr><tr>'
        }
        var idx = generateRandom(0, 15-i)
        var img = cards.splice(idx,1)

        cardTableCode += '<td id="card' + i + '" style="border:solid #9DCEFF; width: 110px; height: 110px;"><img src="'+img+'" style="width: 100px;"><span>?</span></td>'
    }
    cardTableCode += '</tr>'
    $('#cardTable').html(cardTableCode)
}

// 
function hiddenCards(){
    $('#cardTable td img').hide()
    $('#cardTable td span').show()
}

//  
function startGame() {
    var sec = 5

    $('#info').hide()   //   
    scoreInit()    //  
    setTable()  // 
    $('#cardTable td span').hide()

    //  
    function setText(){
        $('#countDown').text(--sec)
    }
    //  
    var intervalID = setInterval(setText, 1000)
    setTimeout(function(){
        clearInterval(intervalID)
        $('#countDown').text('start')
        hiddenCards()
        gameState = ''
    }, 5000)
}

//  
$(document).on('click','#cardTable td' ,function(){
    if(gameState != '') return //     return
    if(openCardId2 != '') return   //2   return
    if($(this).hasClass('opened'))  return //    return
    $(this).addClass('opened')  //   class
    if(openCardId1 == ''){
        $(this).find('img').show()
        $(this).find('span').hide()
        openCardId1 = this.id
    }else {
        if(openCardId1 == openCardId2)  return //    return

        $(this).find('img').show()
        $(this).find('span').hide()

        var openCardSrc1 = $('#'+openCardId1).find('img').attr('src')
        var openCardSrc2 = $(this).find('img').attr('src')
        openCardId2 = this.id

        if(openCardSrc1 == openCardSrc2) {  //
            openCardId1 = ''
            openCardId2 = ''
            scorePlus()
            if(++openedCtn == 8){
                alert('!! \n'+score+' !')
            }
        }else { //
            setTimeout(back, 1000)
            scoreMinus()
        }
    }
})

//    
function back() {
    $('#'+openCardId1).find('img').hide()
    $('#'+openCardId1).find('span').show()
    $('#'+openCardId2).find('img').hide()
    $('#'+openCardId2).find('span').show()
    $('#'+openCardId1).removeClass('opened')
    $('#'+openCardId2).removeClass('opened')
    openCardId1 = ''
    openCardId2 = ''
}

//  
function scoreInit(){
    score = 0
    openedCtn = 0
    $('#score').text(score)
}

//  
function scorePlus(){
    score += 10
    $('#score').text(score)
}

//  
function scoreMinus(){
    score -= 5
    $('#score').text(score)
}

export default {
  methods: {
    gameStart: function () {
      console.log('')
      if (gameState === '') {
        startGame()
        gameState = 'alreadyStart'
      }
    }
  }
}
</script>
<style scoped>
#same_game{
    display: inline-block;
    width: 80%;
    height: auto;
    margin-top: 25px;
}
#infoDiv{
    margin-top: 15px;
    margin-bottom: 15px;
}
button {
    padding: 10px 20px;
    margin-right: 40px;
    margin-bottom: 10px;
}
#countDown {
    width: 474px;
    margin: auto;
    padding: 10px 0;
    font-size: 20px;
    font-weight: bold;
}
#cardTable {
    width: 474px;
    height: 474px;
    margin: auto;
    margin-bottom: 40px;
    border-collapse: collapse;
}
#cardTable td{
    border: solid #9DCEFF;
}
</style>



