<template>
  <div class="home">
    <h1> !!!</h1>
    <h1> ...</h1>
    <button id="return" v-on:click="btn_return"> </button>
  </div>
</template>
<script>
export default {
  name: 'home',
  //  methods   
  methods: {
    btn_return: function (event) {
      this.$router.push('/main')
    }
  }
}
</script>
<style scoped>
  .home{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    width: fit-content;
    height: fit-content;
    font-size: 100%;
  }

  #return{
    margin-top: 10%;
    padding: 15px 25px;
    background-color: #4374D9;
    border: none;
    color: #FFFF;
  }
  #return:hover{
    color: #000000;
  }
</style>



